  mounted() {
    console.log(this.$refs.right_Content.clientHeight);
    this.tableHeight = this.$refs.right_Content.clientHeight - 45;
    this.findSpecificDevice();
    if (this.kanbanConfig.showEnergy) {
      const table4 = this.$refs.right4content;
      const divData4 = table4;
      this.finga4(divData4);
    }
  },
  created() {
    if (this.$store.state.deviceIsJump === true) {
      this.deviceData = this.$store.state.deviceJump;
    }
    var that = this;
    const params = new URLSearchParams(location.search);
    const deviceName = params.get('deviceName');
    this.getDeviceName(deviceName);
    if (KANBAN_CONFIG[this.getUrlKeyMain('cid', window.location.href)]) {
      that.kanbanConfig = KANBAN_CONFIG[this.getUrlKeyMain('cid', window.location.href)];
    }
    if (this.getUrlKeyMain('tvNo', window.location.href) !== null) {
      this.title = that.kanbanConfig.group[this.getUrlKeyMain('tvNo', window.location.href)].title;
      this.groupId = that.kanbanConfig.group[this.getUrlKeyMain('tvNo', window.location.href)].id;
    } else {
      this.title = that.kanbanConfig.title;
    }
    this.getDataTime();
  },
  methods: {
    loadData() {
      this.getState();
      this.findList();
      this.getTOP5Data();
    },
    // 导出图表数据
    exportEchart() {
      const multiHeader = [];
      exportJson2Excel({
        multiHeader,
        header: this.xData.reverse(),
        data: [this.seriesData.reverse()],
        filename: '告警时长TOP5',
        merges: []
      });
    },
    // 查询具体设备
    async findSpecificDevice() {
      try {
        const params = {
          startTime: getTodayFirstDay(),
          endTime: getTodayLastDay(),
          current: 1,
          size: 99999999
        };
        var conditionJson = {
          conditionJson1: [
            { column: 'isCollect', type: 'eq', ptype: 'string', value: 1, operator: 1 }
          ],
          operatorGroup: 1,
          conditionJson2: []
        };
        const res = await deviceGetPageDeviceComplexByConditionAction(params, conditionJson);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.searchOptions[0].options = res.rows.map(item => ({
            label: item.deviceName,
            value: item.deviceCode
          }));
          const params = new URLSearchParams(location.search);
          if (params.has('deviceCode')) {
            this.searchData.groupValue = params.get('deviceCode');
          } else {
            this.searchData.groupValue = res.rows[0].deviceCode;
          }
          this.loadData();
        } else {
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    async findList() {
      try {
        this.mainPage.tableLoading = true;
        // var conditionJson = {
        //   // conditionJson1: [],
        //   // operatorGroup: 1,
        //   // conditionJson2: [],
        //   size: this.mainPage.page.pageSize,
        //   current: this.mainPage.page.currentPage
        // };
        var param = {};
        if (this.searchData.groupValue) {
          param.deviceCode = this.searchData.groupValue;
        } else {
          param.deviceCode = '';
        }
        if (this.searchData.date) {
          param.dateTime = parseTime(this.searchData.date);
        }
        const data = await boardListBreakdownDetail(param, {});
        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.mainPage.data = data.rows;
          this.mainPage.page.total = data.total;
        } else {
          showWarningMessage(data.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        this.mainPage.tableLoading = false;
      }
    },
    exportTable() {
      const multiHeader = [];
      const header = this.mainPage.columns.map(item => {
        if (item.visible !== true) {
          item.disabled = true;
        } else {
          return item.title;
        }
      });
      const filterVal = this.mainPage.columns.map(item => {
        if (item.visible !== true) {
          item.disabled = true;
        } else {
          return item.field;
        }
      });
      const data = formatJson(filterVal, this.mainPage.data);
      exportJson2Excel({
        multiHeader,
        header,
        data,
        filename: '产能明细',
        merges: []
      });
    },
    search() {
      this.getState();
      this.findList();
      this.getTOP5Data();
      console.log(this.searchData);
    },
    // 获取子组件传递过来的设备名称
    getDeviceName(deviceName) {
      this.mainPage.columns.map(item => {
        if (deviceName.indexOf('凸轮') !== -1 && (item.field === 'emptyNumber' || item.field === 'ngNumber' || item.field === 'okNumber' || item.field === 'okRate')) {
          item.visible = false;
        } else if (deviceName.indexOf('视觉') !== -1 && (item.field === 'emptyNumber' || item.field === 'ngNumber' || item.field === 'okNumber' || item.field === 'okRate' || item.field === 'availability' || item.field === 'uph')) {
          item.visible = false;
        } else {
          item.visible = true;
        }
      });
      this.timers = new Date().getTime();// 根据时间戳不同动态加载Table组件
      this.mainPage.data = [];
      this.mainPage.total = 0;
    },
    sortChange(property, order) {
      this.orderByData = {};
      this.orderByData[property] = order;
      this.handleMainPageChange(1, this.mainPage.page.pageSize);
    },
    handleMainPageChange(currentPage, pageSize) {
      this.mainPage.page.currentPage = currentPage;
      this.mainPage.page.pageSize = pageSize;
      // this.findList();
    },
    async getTOP5Data() {
      try {
        var param = {
          pageNum: 1,
          pageSize: 5,
          sortKey: 'warnDuration',
          sortMethod: 'desc',
          deviceCode: this.searchData.groupValue
        };
        if (this.searchData.date) {
          param.startTime = parseTime(this.searchData.date, '{y}-{m}-{d}') + ' 00:00:00';
          param.endTime = parseTime(this.searchData.date, '{y}-{m}-{d}') + ' 23:59:59';
        } else {
          param.startTime = getSearchToday(6) + ' 00:00:00';
          param.endTime = getSearchToday(0) + ' 23:59:59';
        }
        if (this.searchData.groupValue) {
          param.deviceCode = this.searchData.groupValue;
        }
        const res = await dtWarnList(param, {});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 报警信息有5条时,反转;否则填充空数组,拼接成5条
          if (res.rows.map(item => item.warnType).length === 5) {
            const xData = res.rows.map(item => item.warnType).reverse();
            const seriesData = res.rows.map(item => item.warnDuration).reverse();
            const tempseriesData = res.rows.map(item => item.warnDuration).reverse();
            // console.log(xData);
            this.xData = xData;
            this.seriesData = seriesData;
            this.drawTop5(xData, seriesData, tempseriesData);
            console.log(res);
          } else {
            // 计算需要填充的空元素个数并拼接原数组的内容
            const tempxData = res.rows.map(item => item.warnType).reverse();
            const emptyCount = 5 - tempxData.length;
            const tempseriesData = res.rows.map(item => item.warnDuration).reverse();
            const emptyCount2 = 5 - tempseriesData.length;
            // const xData = [...tempxData, ...Array(5 - arr.length).fill("")];
            // const xData = Array(emptyCount).fill('').concat(tempxData);
            const xData = [...Array(emptyCount).fill(''), ...tempxData];
            const seriesData = [...Array(emptyCount2).fill(''), ...tempseriesData];
            // console.log(xData);
            this.xData = tempxData;
            this.seriesData = tempseriesData;
            this.drawTop5(xData, seriesData, tempseriesData);
            console.log(res);
          }
        }
      } catch (error) {
        console.log(error);
      }
    },
    drawTop5(xData, seriesData, tempseriesData) {
      var myColor = ['#FF0033', '#FF0000', '#CC0000', '#B30000', '#800000'];
      // 只显示有数据的外圆 无数据则不显示
      var dataLength = tempseriesData.length;
      console.log(tempseriesData);
      var zeroArray = new Array(dataLength).fill(0);
      const WyArray = [...Array(5 - dataLength).fill(''), ...zeroArray];
      console.log(WyArray);
      var option = {
        // backgroundColor: '#0e2147',
        grid: {
          left: '5%',
          top: '2%',
          right: '2%',
          bottom: '2%',
          containLabel: true
        },
        xAxis: [{
          show: false
        }],
        yAxis: [{
