            },
            lineHeight: 13
            // formatter: function(params) {
            //   var newParamsName = '';
            //   var paramsNameNumber = params.length;
            //   var provideNumber = 10; // 一行显示几个字
            //   var rowNumber = Math.ceil(paramsNameNumber / provideNumber);
            //   if (paramsNameNumber > provideNumber) {
            //     for (var p = 0; p < rowNumber; p++) {
            //       var tempStr = '';
            //       var start = p * provideNumber;
            //       var end = start + provideNumber;
            //       if (p === rowNumber - 1) {
            //         tempStr = params.substring(start, paramsNameNumber);
            //       } else {
            //         tempStr = params.substring(start, end) + '\n';
            //       }
            //       newParamsName += tempStr;
            //     }
            //   } else {
            //     newParamsName = params;
            //   }
            //   return newParamsName;
            // }
          },
          data: xData
        },
        {
          axisTick: 'none',
          axisLine: 'none',
          axisLabel: {
            textStyle: {
              color: '#ffffff',
              fontSize: 16
            }
          },
          data: []
        },
        {
          name: '',
          nameGap: '20',
          nameTextStyle: {
            color: '#ffffff',
            fontSize: 16
          },
          axisLine: {
            lineStyle: {
              color: 'rgba(0,0,0,0)'
            }
          },
          data: []
        }
        ],
        series: [
          {
            name: '条',
            type: 'bar',
            yAxisIndex: 0,
            data: seriesData,
            // label: {
            //   normal: {
            //     show: true,
            //     position: 'right',
            //     textStyle: {
            //       color: '#ffffff',
            //       fontSize: 16
            //     }
            //   }
            // },
            label: {
              normal: {
                show: true,
                formatter: '{name|{b}}',
                rich: {
                  name: {
                    width: '80%',
                    color: '#ffffff',
                    fontSize: 10,
                    padding: [-18, 0, 0, 0]
                  }
                  // value: {
                  //   color: '#ffffff',
                  //   fontWeight: 700,
                  //   fontSize: 14,
                  //   padding: [-15, 0, 0, 0],
                  //   align: 'center'
                  // }
                },
                position: 'insideTopLeft',
                // position: 'left',
                textStyle: {
                  color: '#9AA1A9',
                  fontSize: 14
                },
                offset: [0, -30]
              }
            },
            barWidth: 10,
            itemStyle: {
              normal: {
                color: function(params) {
                  var num = myColor.length;
                  return myColor[params.dataIndex % num];
                }
              }
            },
            z: 2
          },
          {
            name: '白框',
            type: 'bar',
            yAxisIndex: 1,
            barGap: '-100%',
            data: seriesData,
            label: {
              normal: {
                show: true,
                position: 'right',
                textStyle: {
                  color: '#ffffff',
                  fontSize: 12
                }
              }
            },
            barWidth: 20,
            itemStyle: {
              normal: {
                color: 'transparent',
                barBorderRadius: 5
              }
            },
            z: 1
          },
          {
            name: '白框',
            type: 'bar',
            yAxisIndex: 1,
            barGap: '-100%',
            // data: [99.5, 99.5, 99.5, 99.5, 99.5],
            barWidth: 20,
            itemStyle: {
              normal: {
                color: '#0e2147',
                barBorderRadius: 5
              }
            },
            z: 1
          },
          {
            name: '外框',
            type: 'bar',
            yAxisIndex: 2,
            barGap: '-100%',
            // data: [100, 100, 100, 100, 100],
            barWidth: 14,
            itemStyle: {
              normal: {
                color: function(params) {
                  var num = myColor.length;
                  return myColor[params.dataIndex % num];
                },
                barBorderRadius: 5
              }
            },
            z: 0
          },
          {
            name: '外圆',
            type: 'scatter',
            hoverAnimation: false,
            data: WyArray,
            yAxisIndex: 2,
            symbolSize: 18,
            itemStyle: {
              normal: {
                color: function(params) {
                  var num = myColor.length;
                  return myColor[params.dataIndex % num];
                },
                opacity: 1
              },
              z: 2
            }
          }
        ]
      };
      var chartContainer = document.getElementById('chartContainer');
      var chart = this.$echarts.init(chartContainer);
      // 设置初始宽度和高度
      var initWidth = chartContainer.clientWidth; // 初始宽度
      var initHeight = chartContainer.clientHeight; // 初始高度
      chart.resize({
        width: initWidth,
        height: initHeight
      });
      chart.setOption(option, true);
      window.addEventListener('resize', () => {
        // 获取新的宽度和高度
        var newWidth = chartContainer.clientWidth;
        var newHeight = chartContainer.clientHeight;
        chart.resize({
          width: newWidth,
          height: newHeight
        });
      });
    },
    // 显示状态履历模态框
    toHistoryTime(param) {
      this.dialogVisible_time = true;
      var arr = [];
      for (var i = 0; i < this.timeList.length; i++) {
        if (this.timeList[i].state === param) {
          arr.push(this.timeList[i]);
        }
      }
      this.tableData_time = arr;
      this.tablePage_time.total = arr.length;
    },
    handleClose_time() {
      this.dialogVisible_time = false;
      // this.hackReset = false;
    },
    /**
        * @description: 获取设备24小时状态
        * @param obj ztree对象
        * @return: 无
        *@author: CH
        */
    async getState() {
      try {
        // this.tableLoadings = true;
        const res = await GetOneDayDetailStateClock({
          deviceCode: this.searchData.groupValue,
          time: this.searchData.date
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // this.showData = res.rows;
          if (res.rows.cumulativeProduction) {
            // this.cumulativeProduction = res.rows.cumulativeProduction;
            if (getSearchToday(0) === this.stateDataTime) {
              if (res.rows.currentShiftName) {
                this.cumulativeProduction = res.rows.currentValue;
              } else {
                this.cumulativeProduction = res.rows.cumulativeProduction;
              }
            } else {
              this.cumulativeProduction = res.rows.cumulativeProduction;
            }
          } else {
            this.cumulativeProduction = 0;
          }
          // var color = [];
          var echartsData = [];
          this.timeList = res.rows.timeList;
          for (var i in res.rows.timeList) {
            // echartsData[i].name = res.rows[i].state;
            // echartsData[i].value = res.rows[i].time;
            if (res.rows.runTimeSum) {
              this.stateData[0].value = this.timeStamp(res.rows.runTimeSum);
            } else {
              this.stateData[0].value = '0分钟';
            }
            if (res.rows.idleTimeSum) {
              this.stateData[1].value = this.timeStamp(res.rows.idleTimeSum);
            } else {
              this.stateData[1].value = '0分钟';
            }
            if (res.rows.offlineTimeSum) {
              this.stateData[2].value = this.timeStamp(res.rows.offlineTimeSum);
            } else {
              this.stateData[2].value = '0分钟';
            }
            if (res.rows.warnTimeSum) {
              this.stateData[3].value = this.timeStamp(res.rows.warnTimeSum);
            } else {
              this.stateData[3].value = '0分钟';
            }
            if (res.rows.stopTimeSum) {
              this.stateData[4].value = this.timeStamp(res.rows.stopTimeSum);
            } else {
              this.stateData[4].value = '0分钟';
            }
            if (res.rows.timeList[i].state === 0) {
              // color.push('#FFFFFF');
              echartsData.push({
                name: res.rows.timeList[i].state,
                value: res.rows.timeList[i].time,
                itemStyle: { color: '#FFFFFF' }
              });
            }
            if (res.rows.timeList[i].state === 1) {
              // this.stateData[0].name = res.rows[i].state;
              // this.stateData[0].value = this.timeStamp(res.rows[i].time);
              // color.push('#027cf8');
              echartsData.push({
                name: res.rows.timeList[i].state,
                value: res.rows.timeList[i].time,
                itemStyle: { color: '#027cf8' }
              });
            }
            if (res.rows.timeList[i].state === 2) {
              echartsData.push({
                name: res.rows.timeList[i].state,
                value: res.rows.timeList[i].time,
                itemStyle: { color: '#fed016' }
              });
            }
            if (res.rows.timeList[i].state === 3) {
              echartsData.push({
                name: res.rows.timeList[i].state,
                value: res.rows.timeList[i].time,
                itemStyle: { color: '#5b6069' }
              });
            }
            if (res.rows.timeList[i].state === 4) {
              echartsData.push({
                name: res.rows.timeList[i].state,
                value: res.rows.timeList[i].time,
                itemStyle: { color: '#c84747' }
              });
            }
            if (res.rows.timeList[i].state === 5) {
              echartsData.push({
                name: res.rows.timeList[i].state,
                value: res.rows.timeList[i].time,
                itemStyle: { color: '#ff6600' }
              });
            }
          }
          this.showEcharts_State(echartsData);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
        * @description: 处理时间显示
        * @param obj ztree对象
        * @return: 无
        *@author: CH
        */
    timeStamp(StatusMinute) {
      var day = parseInt(StatusMinute / 60 / 24);
      var hour = parseInt((StatusMinute / 60) % 24);
      var min = parseInt(StatusMinute % 60);
      var value = '';
      if (day === 1) {
        value = '24' + VARIABLE.HOUR_NAME + '0' + VARIABLE.MINUTE_NAME;
      }
      if (hour > 0) {
        value += hour + VARIABLE.HOUR_NAME;
      }
      if (min > 0) {
        value += parseFloat(min) + VARIABLE.MINUTE_NAME;
      }

      if (StatusMinute % 1 > 0) {
        value += parseInt((StatusMinute % 1) * 60) + '' + '秒';
      }

      return value;
    },
    /**
        * @description:展示24小时状态图
        * @param {type}
        * @return:
        * @author: CH
        */
    showEcharts_State(echartsData) {
      var myChart = this.$echarts.init(document.getElementById('showEhcarts'));
      // 绘制图表
      myChart.setOption({
        series: [
          {
            name: '',
            type: 'pie',
            radius: ['60%', '80%'],
            hoverAnimation: false,
            // avoidLabelOverlap: false,
            label: {
              normal: {
                show: false,
                position: 'center'
              },
              emphasis: {
                show: false
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            // color: color,
            data: echartsData
          }
        ]
      });
    },
    finga(divData) {
      this.timer = setInterval(() => {
        // 元素自增距离顶部1像素
        divData.scrollTop += 1;
        // 判断元素是否滚动到底部(可视高度+距离顶部=整个高度)
        if (divData.clientHeight + divData.scrollTop === divData.scrollHeight) {
          // 重置table距离顶部距离
          clearInterval(this.timer);
          setTimeout(() => {
            divData.scrollTop = 0;
          }, 2000);
          setTimeout(() => {
            this.finga(divData);
          }, 4000);
        }
      }, 50); // 滚动速度
    },
    finga4(divData) {
      this.timer4 = setInterval(() => {
        // 元素自增距离顶部1像素
        divData.scrollTop += 1;
        // 判断元素是否滚动到底部(可视高度+距离顶部=整个高度)
        if (divData.clientHeight + divData.scrollTop === divData.scrollHeight) {
          // 重置table距离顶部距离
          clearInterval(this.timer4);
          setTimeout(() => {
            divData.scrollTop = 0;
          }, 2000);
          setTimeout(() => {
            this.finga4(divData);
          }, 4000);
        }
      }, 50); // 滚动速度
    },
    // 名称格式化
    nameFormat(value, cutter, newline) {
      var s = value;
      var num0 = cutter;
      var num1 = cutter;
      var flag = 1; // 换行标记
      if (s.length > 2 * cutter + 1) {
        var rs = s.split('|');
        if (rs.length > 1) {
          if (rs[0].length < cutter) {
            num0 = rs[0].length;
            num1 = 2 * cutter - rs[0].length;
          }
          if (rs[1].length < cutter) {
            num0 = 2 * cutter - rs[1].length;
            num1 = rs[1].length;
          }
          rs[0] = rs[0].substring(0, num0);
          rs[1] = rs[1].substring(0, num1);
          if (rs != null) {
            s = rs.join('|');
          }
        }
      } else {
        // eslint-disable-next-line no-redeclare
        var rs = s.split('|');
        if (rs.length > 1) {
          if (rs[0].length < cutter && rs[1].length < cutter) {
            flag = 0;
          }
          if (rs != null) {
            s = rs.join('|');
          }
        }
      }
      if (newline === 1) {
        if (flag === 1) {
          s = s.substring(0, s.length / 2) + '\n' + s.substring(s.length / 2);
        } else if (flag === 0) {
          s = s.split('|').join('\n');
        }
      }
      return s;
    },
    // 看板时间
    tick() {
      var objD = new Date();
      var yy = objD.getYear();
      if (yy < 1900) yy = yy + 1900;
      var MM = objD.getMonth() + 1;
      if (MM < 10) MM = '0' + MM;
      var dd = objD.getDate();
      if (dd < 10) dd = '0' + dd;
      var hh = objD.getHours();
      if (hh < 10) hh = '0' + hh;
      var mm = objD.getMinutes();
      if (mm < 10) mm = '0' + mm;
      var ss = objD.getSeconds();
      if (ss < 10) ss = '0' + ss;
      this.time = yy + ' 年 ' + MM + ' 月 ' + dd + ' 日 ' + hh + ' : ' + mm + ' : ' + ss;
    },
    // 看板时间
    getDataTime() {
      // $.ajax({
      //   type: 'GET',
      //   url: 'http://oa.junhepumps.com:8081/idevcloudwebsub48/junheProductionTask/getPageDeviceRealInformation.action?pageNum=1&pageSize=74',
      //   data: {},
      //   dataType: 'jsonp',
      //   success: function(data) {
      //     console.log('请求成功', data);
      //   },
      //   error: function(data) {
      //     console.log('请求失败', data);
      //   },
      //   crossDomain: true,
      //   xhrFields: { withCredentials: true }
      // });
      this.dataTime = new Date($.ajax({ type: 'HEAD', async: false }).getResponseHeader('Date')).getTime();
      console.log('时间', $.ajax({ type: 'HEAD', async: false }).getResponseHeader('Date'));
      this.toGetTime();
    },
    toGetTime() {
      setInterval(() => {
        var date = new Date(this.dataTime);
        var y = date.getFullYear();
        var M = date.getMonth() < 9 ? '0' + (date.getMonth() + 1) : (date.getMonth() + 1);
        var d = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
        var h = date.getHours() < 10 ? '0' + date.getHours() : date.getHours();
        var m = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes();
        var s = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds();
        this.time = y + ' 年 ' + M + ' 月 ' + d + ' 日   ' + h + ':' + m + ':' + s;
        this.dataTime += 1000;
      }, 1000);
    },
    showLogo() {
      return 'logo_' + this.kanbanConfig.logo;
    },
    showLogo2() {
      if (this.getUrlKeyMain('cid', window.location.href) === '161') {
        return 'logo_lt2';
      } else {
        return '';
      }
    },
    showLogo3() {
      if (this.getUrlKeyMain('cid', window.location.href) === '161') {
        return 'logo_ry2';
      } else {
        return '';
      }
    },
    getUrlKeyMain(name, url) {
      // eslint-disable-next-line no-sparse-arrays
      return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(url) || [, ''])[1].replace(/\+/g, '%20')) || null;
    },
    formatDateThree(time) {
      var date = '';
      var D = '';
      date = new Date(time);
      var M = date.getMonth() < 9 ? '0' + (date.getMonth() + 1) : (date.getMonth() + 1);
      var d = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
      D = M + '-' + d;
      return D;
    },
    limitMoveNum() {
      let num = 0;
      this.productTaskData.forEach(item => {
        num += item.length;
      });
      return num > 2 ? 1 : 3;
    }
  }

};
</script>

